.toy-details {
    @include flex-columm-center;
    margin-top: 20px;

    .btn-back {
        position: relative;
        left: -42vw;
        top: -3vh;
    }

    span {
        color: $clr2;
    }

    h1 {
        font-size: 1.7em;
        margin-bottom: .3em;
    }

    h2 {
        font-size: 1.4em;
        margin-bottom: .3em;
    }

    h3 {
        font-size: 1.2em;
    }

    p {
        margin-top: 0;
    }

    img {
        height: 25vh;
    }

    input,
    textarea {
        border-radius: 7px;
        border: 1px solid darken($clr5 , 15%);
        padding: 5px;
        font-family: Comfortaa;
        font-size: 11px;
    }

    .toy-msgs {
        padding: 5px;
        display: flex;
        flex-direction: column;
        align-items: center;
        border: 1px solid darken($clr5 , 15%);
        max-height: fit-content;


        li {
            list-style: none;
            display: flex;
            justify-content: space-between;
            padding: 3px 0;

            span {
                font-weight: bold;
                margin-right: 10px;
            }

            button {
                margin-left: 10px;
                align-self: flex-end;
                font-size: 12px;
            }
        }
    }

    .toy-reviews {
       @include reviews;

        form {
            display: flex;
            flex-direction: column;

            >* {
                margin: 10px 0;
            }

            textarea {
                min-width: 250px;
                height: 80px;
            }
        }

    }

    @include for-narrow-layout {
        .review-msg-area {
            display: flex;
             >*{
                margin:  0 20px;
             }
        }

    }
}