@mixin flex-columm-center {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@mixin reviews {
  padding: 5px 0;
  margin: 15px 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid darken($clr5 , 15%);

  .reviews {
    display: flex;
    flex-direction: column;
    width: 100%;

    .review {
      background-color: darken($clr5 , 10%);
      width: 100%;
      margin: 5px 0;

      h3 {
        text-align: center;
        margin: 5px 0;
      }

      p {
        font-size: 10px;
        margin: 5px 0;
      }
    }

  }
}

@mixin for-mobile-layout {
  @media (max-width: $break-narrow) {
    @content;
  }
}


@mixin for-narrow-layout {
  @media (min-width: $break-narrow) {
    @content;
  }
}

@mixin for-normal-layout {
  @media (min-width: $break-normal) {
    @content;
  }
}

@mixin for-wide-layout {
  @media (min-width: $break-wide) {
    @content;
  }
}

@mixin layoutGTC($max) {
  grid-template-columns:
    minmax($layoutPadding, 1fr) minmax(auto, $max) minmax($layoutPadding, 1fr);
}