.main-nav {
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    background-color: lighten($clr2 , 10%);
    align-items: center;
    width: 100vw;

    a {
        text-decoration: none;
        border-right: 1.5px solid darken($clr1, 10%);
        color: $clr5;
        width: 20%;
        text-align: center;
        padding: .5em 0;
        font-size: 1.1em;
        font-weight: 600;
        text-transform: uppercase;

        &.active {
            color: $clr2;
            background-color: lighten($color: $clr1, $amount: 10%);
        }

        &:hover {
            color: $clr2;
            background-color: lighten($color: $clr1, $amount: 10%);
        }
    }

    :first-child {
        border-left: 1.5px solid $clr5;
    }

}